.latest-news {
    background-color: #fff;
    margin: 5vh auto;
    h1 {
        font-weight: 400;
        text-align: center;
        font-size: 4.7rem;
        margin-bottom: 5rem;
    }
    .latest-news_wrapper {
        width: 92.5%;
        margin: 2.5vh auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
        .news-article {
            width: 25.1%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            .news-thumbnail {
                object-fit: cover;
                width: 100%;
                height: auto;
                border-radius: 60px;
                cursor: pointer;
            }
            .news-heading {
                font-weight: 400;
                font-size: 2.1rem;
                margin: 1.25rem 0;
                line-height: calc(2.1rem * 1.2);
                cursor: pointer;
                &:hover {
                    text-decoration: underline;
                }
            }
            .news-description {
                font-size: 1.1rem;
                text-align: left;
                line-height: calc(1.1rem * 1.4);
            }
        }
    }
}