header {
    padding: 1.15rem 0;
    background-color: #F4F4F2;
    z-index: 100;
    position: fixed;
    width: 100%;
    nav {
        width: 90%;
        margin: 0.75rem auto 0;
        display: flex;
        justify-content: space-around;
        flex-direction: row;
        user-select: none;
        .logo {
            cursor: pointer;
            height: 6vh;
            width: auto;
        }
        .nav-menu {
            margin: 0.25rem 7.5rem 0;
            width: 33.3333%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            p {
                cursor: pointer;
                font-weight: 500;
                font-size: 0.9rem;
                text-transform: uppercase;
                transition: .225s;
                padding-bottom: 0.6rem;
                border-bottom: 2px solid transparent;
                &:hover {
                    color: rgb(75, 75, 75);
                    border-bottom: 2px solid orange;
                }
            }
        }
        button {
            background-color: black;
            color: white;
            font-weight: 500;
            border: none;
            outline: none;
            display: flex;
            justify-content: center;
            align-items: center;
            text-transform: uppercase;
            cursor: pointer;
            padding: 0.825rem 1.5rem;
            transition: .15s ease-in-out;
            margin-bottom: 0.75rem;
            &:hover {
                background-color: rgb(30, 30, 30);
            }
            .arrow-icon {
                font-size: 1.65rem;
                margin-left: 0.75rem;
                color: rgb(195, 139, 33);
            }
        }
    }
}