.mainPage {
    padding-top: 12.5vh;
    padding-bottom: 7.5vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #F4F4F2;
    .main-video {
        object-fit: cover;
        width: 82.5%;
        height: 82.5%;
        border-radius: 80px;
        filter: brightness(0.8);
    }
    .video-text {
        width: 100%;
        height: 100%;
        text-align: center;
        user-select: none;
        display: flex;
        flex-direction: column;
        z-index: 50;
        top: 100%;
        left: 50%;
        transform: translate(-50%, -52.5%);
        position: absolute;
        color: #fff;
        h1 {
            font-weight: 400;
            font-size: 5.25rem;
            margin-bottom: 1.5rem;
            line-height: calc(5.25rem * 1.2);
            letter-spacing: -2.5px;
        }
        p {
            width: 35%;
            margin: 0 auto;
            font-size: 1.25rem;
            line-height: calc(1.25rem * 1.35);
        }
        .icon-wrapper {
            margin-top: 12%;
            span {
                display: block;
                text-transform: uppercase;
                font-size: 0.825rem;
            }
            .icon {
                cursor: pointer;
                margin-top: 0.6rem;
                color: orange;
                font-size: 1.6rem;
            }
        }
    }
}