.vehicleIntroduction {
    background-color: #fff;
    text-align: center;
    margin: 12.5vh auto;
    h2 {
        font-weight: 600;
        font-size: 4.5rem;
        line-height: calc(4.5rem * 1.3);
        letter-spacing: -1px;
        word-spacing: 7.5px;
    }
    p {
        font-size: 1.25rem;
        width: 33.33333%;
        margin: 1.5rem auto 0;
        line-height: calc(1.25rem * 1.35);
    }
    img {
        object-fit: cover;
        width: 50%;
        user-select: none;
    }
    .vehicle-stats-wrapper {
        display: flex;
        justify-content: space-between;
        width: 60%;
        margin: 0 auto 5vh;
        .vehicle-stats_item {
            width: 33.3333%;
            p {
                color: rgb(100, 100, 100);
                font-size: 0.915rem;
                margin-bottom: 0.3rem;
            }
            h3 {
                font-size: 3.5rem;
                font-weight: 600;
                span {
                    color: rgb(75, 75, 75);
                    font-size: 1.25rem;
                    font-weight: 400;
                }
            }
        }
    }
    button {
        background-color: black;
        color: white;
        font-weight: 500;
        font-size: 0.875rem;
        border: none;
        outline: none;
        text-transform: uppercase;
        margin: 0 auto;
        display: flex;
        cursor: pointer;
        padding: 1.5rem 2.75rem;
        transition: .15s ease-in-out;
        margin-bottom: 0.75rem;
        &:hover {
            background-color: rgb(30, 30, 30);
        }
        .arrow-icon {
            font-size: 1.1rem;
            margin-left: 1rem;
            color: rgb(195, 139, 33);
        }
    }
    .order-vehicle-container {
        margin: 7.5vh 0;
        video {
            object-fit: cover;
            width: 100%;
            height: 67.5vh;
            object-position: top;
            filter: brightness(0.35);
        }
        .order-vehicle {
            padding: 6.25rem 0 5rem;
            background: #000;
            color: #fff;
            margin-top: -1vh;
            h2 {
                font-weight: 400;
                font-size: 4.75rem;
                line-height: calc(4.5rem * 1.3);
                letter-spacing: -2px;
            }
            p {
                letter-spacing: 0.25px;
                font-size: 1.325rem;
                line-height: calc(1.3rem * 1.4);
            }
            .order-vehicle-button {
                background-color: #fff;
                color: #000;
                font-weight: 500;
                font-size: 0.9rem;
                border: none;
                outline: none;
                text-transform: uppercase;
                margin: 5rem auto 0;
                display: flex;
                align-items: center;
                cursor: pointer;
                padding: 1.1rem 2rem;
                transition: .15s ease-in-out;
                margin-bottom: 0.75rem;
                .arrow-icon {
                    font-size: 1.5rem;
                    margin-left: 0.75rem;
                }
                &:hover {
                    background-color: rgb(220, 220, 220);
                }
            }
        }
    }
}